body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.wrapper {
    padding-top: 50px;
    background: #ECEFF1;
    height: 100%;
    display: flex;
    flex-flow: row wrap;
    padding: 20px;
    justify-content: space-around;
    align-content: flex-start;
    overflow: auto;
  }
.image {
    background: #fff;
    border-radius: 2px;
    height: 250px;
    margin: 1rem;
    position: relative;
    width: 250px;
    overflow: hidden;
  }
.App {
    text-align: center;
    background-color: rgb(187, 19, 19);
  }
  
  .App-logo {
    text-align: left;
    float: left;
  }
  
  .App-header {
    background-color: rgb(5, 90, 43);
    height: 100px;
    padding: 10px;
    color: white;
  }
  
  .sticky {
    position: fixed;
    top: 0;
    width: 100%
  }
  
  .App-title {
    font-size: 1.5em;
    float: right;
  }
  
  .App-intro {
    font-size: large;
  }
